/* =================
		LISTS
================= */

.field.body,.field-oc-long-text{
	ul,ol {
		//overflow hidden is needed for lists next to floated elements
		overflow: hidden;
		margin: .5em 0 1.5em;
		padding: 0 0 0 30px;
		ul,ol{
			margin: .5em 0 .5em;
			padding: 0 0 0 20px;
		}
	}
}

ul {
	list-style-type: disc;
}

ol {
	list-style-type: decimal;
	&[type="A"]{
		list-style-type: lower-alpha;
	}
	&[type="I"]{
		list-style-type: upper-roman;
	}
}
