  font-style: italic;
}

.gm-style-iw h2 {
  font-size: 16px;
}

.gm-style-iw h3 {
  font-size: 14px;
}

/* SKIP NAV
------------- */
.overflow-guard > a.focusable:focus {
  position: fixed !important;
  top: 0;
  left: 0;
  z-index: 9999;
  display: block;
  width: 100%;
  background: #fff;
  padding: .25em 1em;
}

/* KINTING
-------------- */
.kint {
  position: fixed;
  z-index: 9999;
  top: 200px;
  right: 5%;
  width: 90%;
  max-width: 800px;
  max-height: -webkit-calc(90% - 200px);
  max-height: -moz-calc(90% - 200px);
  max-height: -o-calc(90% - 200px);
  max-height: calc(90% - 200px);
  overflow: scroll;
}

/* BACK TO TOP
----------------- */
.back-anchor {
  display: none;
  position: fixed;
  bottom: 3%;
  right: 3%;
  box-shadow: 3px 3px 5px rgba(35, 35, 35, 0.35);
  z-index: 9999;
  border-radius: 50%;
}

.back-anchor a {
  display: block;
  width: 50px;
  height: 50px;
  background: rgba(35, 35, 35, 0.85);
  border-radius: 50%;
  position: relative;
  text-align: center;
}

.back-anchor a i {
  color: #fff;
  font-size: 45px;
  line-height: 45px;
}

.back-anchor a span {
  position: absolute;
  left: -900vw;
  top: 0;
  font-size: 1px;
  opacity: .1;
}

