/* ===============
	SELECT INPUT
==================== */

@mixin inputSelect{
	width: 100%;
	font-size: 16px;
	border: 1px solid $borderRule;
	border-radius: 0;
	height: auto;
	&.select2--container--focus{
		box-shadow: 0 2px 2px rgba(35,35,35,0.3);
	}
}

body select,body select:hover,body select:focus{
	background: none;
}

/* SELECT STYLING
------------------- */
//hide optgroup safari bug fix
optgroup:empty{
	display: none;
}

//hide custom optgroup labels (ios10 bug fix)
.select2-results__group{
	display: none !important;
}

//override script set width
.select2-container--default{
	width: 100% !important;
	max-width: 250px;
}
body .select2-container{
	@include spacer(0);
}

/* SINGLE SELECT 2
----------------- */
body .select2-container--default{
	//single filter
	.select2-selection--single{
		@include inputSelect;
		.select2-selection__choice__remove{
			position: relative;
			z-index: 999;
		}
		//rendered selection
		.select2-selection__rendered{
			padding: 4px 32px 4px 10px;
			color: $darkGrey;
			text-align: left;
		}
		.select2-selection__arrow{
			@include triangle(down,12px,16px,$midGrey);
			top: 14px;
			right: 10px;
			//hide default select2 arrow
			b{
				display: none;
			}
		}

	}//end single
}


/* SELECT 2 MULTI
------------------ */
body .select2-container--default{

	//multi filter
	.select2-selection--multiple{
		@include inputSelect;
		padding: 3px 0 3px 0;
		.select2-selection__choice__remove{
			position: relative;
			z-index: 999;
		}
		//selection input
		.select2-search--inline{ 
			position: relative;
			float: none;
			padding: 2px 3px;
			&:after{
				content: '';
				display: block;
				height: 100%;
				position: absolute;
				top: 10px;
				right: 2px;
				cursor: pointer;
				@include triangle(down,12px,16px,$midGrey);
			}
			input.select2-search__field{
				color: $darkGrey;
				@include placeholder;
				&:focus{
					@include placeholderFocus;
				}
			}
		}

		//selected item
		.select2-selection__choice{
			border-radius: 0;
			background-color: $offWhite;
		}
	}//end multi
}

/* SELECT 2 ACTIVE
-------------------- */
body .select2-container--default{
	//active select2
	&.select2-container--open,&.select2-container--focus{

		//single
		.select2-selection--single{
			.select2-selection__arrow{
				border-top: 0;
				border-bottom: 12px solid $midGrey;
			}
		}

		//multiple
		.select2-selection--multiple{			
			.select2-search--inline{ 
				&:after{
					border-top: 0;
					border-bottom: 12px solid $midGrey;
				}
				input.select2-search__field{
					@include placeholder;
					&:focus{
						@include placeholderFocus;
					}
				}
			}
		}

	}//end active
}


/* SELECT 2 DROPDOWNS
---------------- */
.select2-container{
	.select2-dropdown{
		border: 1px solid $borderRule;
		border-radius: 0;
		border-top: 0;
		padding: 0;
	}
}
.select2-container--default .select2-results__option[aria-selected="true"]{
	background-color: $offWhite;
}
.select2-container--default .select2-results__option--highlighted[aria-selected]{
	background-color: $link !important; 
}


/* PATTERN LAB ONLY
---------------------- */
.pl .sg-pattern-example{
	.select2-container{
		max-width: 400px;
	}
}